@media only screen and (max-width: 600px) {
  .content-description{
    margin-top: 50px !important;
  }
  .card{
    width: 100% !important;
  }
  .main-CV h3{
    position: absolute;
    margin-top: 40px !important;
    margin-right: -15px !important;
    font-size: 20px;
    color: white;
  }
  .cv{
    background-image: url("../../images/cv.png");
    background-repeat: no-repeat;
    background-size: contain;
    position: absolute;
    width: 70px !important;
    z-index: 5;
    margin-right: 20px;
    margin-top: 70px !important;
    cursor: pointer;
    padding: 0;
  }
  .github{
    width: 30px !important;
  }
  .linkedin{
    width: 30px !important;
  }
}
@media only screen and (width: 320px) {
  .content-description{
    margin-top: 50px !important;
    width: 370px;
    margin-left: -50px;
  }
  .github{
    width: 30px !important;
  }
  .linkedin{
    width: 30px !important;
  }
  .main-CV h3{
    font-size: 16px;
    color: white;
    margin-right: 70px !important;
    margin-top: 60px !important;
    display: none;
  }
  .cv{
    margin-right: 100px !important;
    margin-top: 50px !important;
  }
  .navbar{
    display: none;
  }
  .email{
    background-color: rgba(118, 118, 250, 0.719);
    color: white;
    font-size: 17px !important;
    border-radius: 20px;
    font-weight: 500;
  }
}
@media only screen and (width: 375px) {

  .main-CV h3{
    font-size: 16px;
    color: white;
    margin-right: 70px !important;
    margin-top: 60px !important;
    display: none;
  }
  .cv{
    margin-right: 130px !important;
    margin-top: 60px !important;
  }
  .navbar{
    display: none;
  }
  .email{
    background-color: rgba(118, 118, 250, 0.719);
    color: white;
    font-size: 18px !important;
    border-radius: 20px;
    font-weight: 500;
  }
}
@media only screen and (width: 425px) {
  .cv{
    width: 70px !important;
    margin-top: 50px !important;
    margin-right: -10px !important;
  }
  .main-CV h3{
    display: none;
  }

  .navbar{
    display: none;
  }
}
@media only screen and (width: 768px) {
  .navbar{
    display: none;
  }
  .ico-skater{
    display: flex;
    justify-content: center;
  }
  .char-stand{
    display: none !important;
  }
  .list-hobies {
    display: none;
  }

  .email{
    background-color: rgba(118, 118, 250, 0.719);
    color: white;
    font-size: 22px;
    border-radius: 20px;
    font-weight: 500;
  }
}

.wideBackground{
  background-image: url("../../images/alone2.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
}

.content-description{
  display: flex;
  justify-content: center;
  margin-top: 250px;
}
.main-boxCard{
  display: flex;
  justify-content: center;
  align-items: flex-end;
}
.sgv{
  padding: 0px;
  margin: 0px;
  z-index: 0;
}
.item-linked-in{
  display: flex;
  justify-content: center;
}
.linkedin:hover{
  width: 55px;
  height: 55px;
}
.back-rounded{
  width: 80px;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.linkedin{
  background-image: url('../../images/linkedin.png');
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  width: 50px;
  height: 50px;
  cursor: pointer;
}

.item-github{
  display: flex;
  justify-content: center;
}
.github{
  background-image: url('../../images/github.png');
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  width: 50px;
  height: 50px;
  cursor: pointer;
  
}
.github:hover{
  width: 55px;
  height: 55px;
  position: absolute;
}

/*EFFECT CV ---- START ----*/
.main-CV{
  display: flex;
  justify-content: flex-end;
}
.main-CV h3{
  position: absolute;
  margin-top: 200px;
  color: white;
  cursor: pointer;
  margin-right: -15px;
}
.main-CV h3:hover{
  color: blue;
}
.cv:hover{
  color: blue;
  width: 210px;
  height: 210px;
  margin-right: -5px;
}
.cv{
  background-image: url("../../images/cv.png");
  background-repeat: no-repeat;
  background-size: contain;
  position: absolute;
  width: 200px;
  height: 200px;
  z-index: 5;
  margin-top: 240px;
  cursor: pointer;
  padding: 0;
}
.cv:hover{
  border-bottom: 1px solid white;
}



/*EFFECT CV ---- END ---- */

.card-body{
  display: flex !important;
  justify-content: center;
}
.webdev{
  width: 100%;
  display: flex !important;
  justify-content: center;
}
.email{

  color: white;
  font-size: 22px;

  font-weight: 500;
}
.main-email{
  display: flex;
  justify-content: center;
  width: 100% !important;
}
.sub-main-email{
  width: 50%;
  height: 35px;
  background-color: rgba(118, 118, 250, 0.719);
  border-radius: 0px 0px 10px 10px;
}