  .hobie-mainUl ul{
    height: 50px !important;
  }
  #e8806aae-1dd3-4f44-829b-8bd04a0ec3ea{
    width: 200px ;
    height: 200px;
    margin-left: 20px;
  }
  .ico-skater{
    display: flex;
    justify-content: center !important;
  }
  .hobies{
    margin-top: 30px;
  }
