.svg-about{
  margin-top: -10px;
}
@media only screen and (min-device-width: 1202px) and (max-device-width: 1800px) {
  .hobie-mainUl ul{
    height: 50px !important;
  }
  #e8806aae-1dd3-4f44-829b-8bd04a0ec3ea{
    width: 450px !important;
  }
  .hobies{
    margin-top: 30px;
    height: 150px;
  }
  .made-by{
    margin-top: 200px !important;
    margin-left: 700px !important;
  }
  .comp-text {
    margin-left: 5px;
  }
}
.svg-about{
  margin-top: -10px;
}
.title-presentation{
  background-color: black;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 20px;
  height: 80px;
  margin-top: 30px;
  border: 2px solid black;
}
.icon-info{
  background-image: url("../../images/information.png");
  background-repeat: no-repeat;
  background-size: contain;
  width: 50px;
  height: 50px;
}
.char-stand{
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding: 0;
  height: auto;
}
.hobie-mainUl ul{
  display: flex;
  list-style: none;
  height: 250px;
}
.list-hobies{
  display: flex;
  justify-content: space-around;
  align-items: flex-end;
  padding: 0px;
  margin: 0px;
}
.svg-bow-aboutme{
  margin-top: -200px;
}
.navbar{
  position: fixed;
  z-index: 3;
  width: 100%;
}

.navbar-collapse{
  display: flex;
  justify-content: center;
}
.navbar-nav li{
  margin-left: 100px;
  margin-right: 100px;
  cursor: pointer;
  font-weight: 700;
}
.navbar-nav li:hover{
  border-bottom: 1px solid blue;
}
.ico-skater{
  margin: 0px;
  padding: 0px;
}
